<template>
    <div class="Head">
        <div class="t-left">
            <a-row type="flex">
                <a-col :span="12">
                    <router-link to="/"><img alt="Vue logo" src="../assets/logo.png" height="64px" style="margin-right:20px;"></router-link>

                    <router-link to="/">Home</router-link>
                    <a-divider type="vertical" />
                    <router-link to="/futures">Futures</router-link>
                    <a-divider type="vertical" />
                    <router-link to="/market">Market</router-link>
                    <a-divider type="vertical" />
                    <router-link to="/finance">Finance</router-link>
                    <a-divider type="vertical" />
                    <router-link to="/startup">Startup</router-link>
                </a-col>
                <a-col :span="12" class="t-right">

                    <a-dropdown :trigger="['click']">
                        <a class="ant-dropdown-link" @click="e => e.preventDefault()" style="color:#007bff;">
                            Language
                        </a>
                        <a-menu slot="overlay">
                            <a-menu-item v-for="i in lang" :key="i.name">
                                <a href="#">{{i.name}}</a>
                            </a-menu-item>
                        </a-menu>
                    </a-dropdown>
                    <a-divider type="vertical" />

                    <span v-if="!user_info">
                        <router-link to="/login">Login</router-link>
                        <a-divider type="vertical" />
                        <router-link to="/register">Sign Up</router-link>
                    </span>

                    <span v-if="user_info">
                        <router-link v-if="user_info" to="/my/assets">{{user_info.account}}</router-link>
                        <a-divider type="vertical" />
                        <router-link to="/logout">Logout</router-link>
                    </span>

                </a-col>
            </a-row>

        </div>
    </div>
</template>

<script>
export default {
    name: "Head",
    props: {
        msg: String,
    },
    data() {
        return {
            lang: [
                { name: "English" },
                { name: "Français" },
                { name: "Indonesian" },
                { name: "한국인" },
                { name: "日本語" },
                { name: "简体中文" },
            ],
            user_info: null,
        };
    },
    methods: {
        init() {
            if (window.localStorage) {
                let userInfo = window.localStorage["userInfo"];
                if (userInfo) {
                    this.g.userInfo = JSON.parse(window.atob(userInfo));
                    this.user_info = this.g.userInfo;
                }
            }
            this.$EventBus.$on("loginComplete", (userInfo) => {
                this.g.userInfo = userInfo;
                this.user_info = this.g.userInfo;
                if (window.localStorage) {
                    window.localStorage["userInfo"] = window.btoa(
                        JSON.stringify(userInfo)
                    );
                }
            });
            this.$EventBus.$on("logoutComplete", () => {
                this.g.userInfo = null;
                this.user_info = null;
                window.localStorage.clear();
            });
        },
    },
    mounted() {
        this.init();
    },
};
</script>

